.render {
    text-align: left;
    background-color: #131313;
    color: #ffffff;
}
.usdp-Content{
    padding: 50px 11% 20px;
    font-size: 18px;
    background-color: #131313;
}
.cont-tile{
    font-size: 28px;
}
.usdp-title{
    margin-top: 16px;
    font-size: 28px;
    color: #fec715;
    text-align: left;
    display: flex;
}

.title-icon{
    margin-left: 16px;
    cursor: pointer;
    margin-top: -6px;
}
.title-notes{
    font-size: 16px;
    color: #666666;
    margin-top: 8px;
}
.cont-title1{
    text-align: center;
    font-size: 28px;
    margin-top: 48px;
}
.cont-img{
    display: flex;
    justify-content: center;
    justify-items: auto;

}
.cont-token{
    width: 390px;
    height: 468px;
    background-image: url("../../assets/img/rune_bg-usdp.png");
    background-size: 100% 100%;
    display: flex;
    justify-content: center;
    align-items:  flex-end;
    padding: 0 100px;
    word-break: break-all;
}
.proportion{
    display: flex;
    justify-content: center;
}
.cont-proportion{
    width: 570px;
    display: flex;
    justify-content: space-between;
}
.progress-cont{
    width: 570px;
}
 :where(.css-dev-only-do-not-override-gzal6t).ant-btn-primary:disabled, :where(.css-dev-only-do-not-override-gzal6t).ant-btn-primary.ant-btn-disabled{
    background-color: #878789;
}
.join-gr{
    background-color: #000000;
    display: flex;
    justify-content: space-between;
    padding: 0 11%;
    height: 68px;
    align-items: center;
}
.join-table{
    margin: 20px 11% 0;
    background-color: #000000;
    border-radius:8px;
}
.table-cont{
    display: flex;
    justify-content: space-between;
    padding: 16px;
}
.cont-span{
    color: #fec715;
}
.cont-t{
    font-size: 22px;
}

.details-table{
    margin: 20px 0;
    background-color: #000000;
    border-radius: 5px;
    padding: 20px 16px;
}
:where(.css-gzal6t).ant-table-wrapper .ant-table{
    background-color: #000000 !important;
    color: #fff !important;
    border: unset !important;
}

.ant-table-thead{
    background-color: #000 !important;
}

