.App-header {
    background-color: #000000;
    min-height: 14vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    border-bottom: 2px solid transparent;
    background-clip: padding-box, border-box;
    background-origin: padding-box, border-box;
    background-image: linear-gradient(to right, rgba(2, 2, 2, 0.98), rgba(2, 2, 2, 0.98)),linear-gradient(90deg, rgba(2, 2, 2, 0.98) 0%, #ffffff 20%, rgba(255, 255, 255, 0) 99%);
}

.link2{
    width: 80px;
    height: 68px;
    line-height: 68px;
    text-align: center;
    margin: 0 40px;
    color: #000000;
    border-radius: 5px;
    padding: 5px 8px;
    text-decoration: none;
    background-color: #fec715;
    font-size: 14px;
}
.link1{
    width: 80px;
    height: 68px;
    line-height: 68px;
    text-align: center;
    padding: 0 40px;
    font-weight: 500;
    color: #fec715;
    text-decoration: none;
}
.link{
    width: 80px;
    height: 68px;
    line-height: 68px;
    text-align: center;
    padding: 0 40px;
    color: #ffffff;
    text-decoration: none;
}
.css-axpu1l {
    position: absolute;
    height: 2px;
    bottom: 0;
    width: 100%;
    -webkit-transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    background-color: #fec715;
}

.App-header :where(.css-dev-only-do-not-override-gzal6t).ant-btn-default{
    background-color: #fec715;
    border: 1px solid #fec715;
}

.App-header :where(.css-dev-only-do-not-override-gzal6t).ant-btn-default:not(:disabled):not(.ant-btn-disabled):hover{
    background-color: #fec715;
    border: 1px solid #fec715;
    color: #000000;
}

.login{
    background: #fec715 !important;
}
